.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  /* background-color: rgb(239, 209, 219); */

 
}

.layout-home {
  background-color: antiquewhite;
}

.home-page .card {
  /* background-color: rgba(128, 128, 128, 0.097);  */
  background-color: rgb(239, 209, 219);

  width: 18rem;
  border: none;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Add some shadow for better appearance */
}

.home-page .card-body {
  background-color: white; /* Ensure the card body has a white background for contrast */
  padding: 10px;
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 200px; /* Adjust height as needed */
  object-fit: cover; /* Ensure images cover the area without distortion */
}

